.hero {
  padding-bottom: 24px;
  border-bottom: 1px solid #D9FFF5; }

.hero {
  padding-top: 24px; }

.hero--swiper {
  padding-left: 16px; }

.hero__item {
  display: inline-block;
  padding: 8px 10px;
  max-width: 246px;
  height: 40px;
  border: 2px solid #B8FFEC;
  border-radius: 6px;
  text-align: center; }

.hero__subtitle {
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: -0.2px;
  color: #7E7E82; }

.hero__heading {
  position: relative;
  margin-bottom: 24px;
  margin-left: 16px; }
  .hero__heading:before {
    content: '';
    position: absolute;
    top: 50%;
    left: -16px;
    transform: translateY(-50%);
    width: 4px;
    height: 40px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    background-color: #41F6D7; }

.hero__text {
  margin-top: 24px;
  margin-bottom: 16px;
  padding: 0 16px;
  height: 90px;
  overflow: hidden; }
  .hero__text .hero__text-content {
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.2px;
    color: #1B1C21; }

.hero__show-link {
  margin-left: 24px;
  padding-left: 16px;
  background-image: url(./static/down.png);
  background-repeat: no-repeat;
  background-position: center left;
  background-size: 7px 10px; }
  .hero__show-link .hero__link-text {
    background-color: transparent;
    border: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px; }
    .hero__show-link .hero__link-text:hover {
      cursor: pointer; }

.hero__main-logo {
  min-height: 176px;
  margin-top: 16px;
  background-image: url(./static/cps.jpg);
  background-size: contain; }
