.header {
  display: flex;
  flex-flow: column;
  justify-content: space-around;
  height: 88px;
  background-color: #FFFFFF;
  border-bottom: 1px solid #D9FFF5; }

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 16px;
  height: 51px; }
  .header__wrapper .header__left {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 152px; }

.header__callback-icons {
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 96px; }
  .header__callback-icons::before {
    content: '';
    position: absolute;
    height: 32px;
    width: 0;
    border: 1px solid #EAEAEA;
    top: 50%;
    left: -20px;
    transform: translate(-50%, -50%); }

.header__btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }
