.price .price__title {
  padding: 16px;
  height: 100%;
  background-color: #FFFFFF; }

.price {
  padding-bottom: 24px;
  border-bottom: 1px solid #D9FFF5; }

.price .price__title {
  height: 100%; }

.price--swiper .swiper-slide {
  margin-top: 16px;
  padding-left: 8px; }

.price__item {
  width: 240px;
  height: 200px;
  background-color: #FFFFFF;
  border-radius: 6px; }
  .price__item-wrapper {
    position: relative;
    padding: 16px 0 0 16px;
    margin-right: 16px; }
    .price__item-wrapper .price--button {
      position: absolute;
      bottom: 0;
      right: 0; }

.item-container {
  height: 52px;
  margin-bottom: 16px; }

.price__item-heading {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.72;
  color: #7E7E82; }

.price__item-text {
  padding-top: 4px;
  margin-bottom: 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  color: #1B1C21; }

.price--button {
  position: absolute;
  bottom: 0;
  right: 0; }

.price__tax-text {
  padding: 16px 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
  color: #1B1C21; }

.price__commercial {
  width: 220px;
  padding-left: 16px; }
  .price__commercial:hover {
    cursor: pointer; }
  .price__commercial svg {
    margin-left: 5px;
    transform: translateY(1px); }
  .price__commercial .price__commercial-link {
    width: 100%;
    text-decoration: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1B1C21; }
