body {
  font-family: 'TTLakes', sans-serif; }

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

ul {
  list-style: none; }

.img {
  width: 100%; }

.btn {
  position: relative;
  width: 40px;
  height: 40px;
  background-color: #FF3E79;
  border: none;
  border-radius: 50%; }
  .btn:hover {
    cursor: pointer; }

.container {
  width: 320px;
  margin: 0 auto;
  background-color: #F8F8F8; }

.subtitle {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-transform: uppercase;
  color: #7E7E82; }

.swiper-pagination {
  position: static;
  margin-top: 10px; }

.button {
  position: relative;
  width: 119px;
  height: 32px;
  background-color: #FF3E79;
  border: none;
  border-radius: 16px;
  font-weight: 600;
  font-size: 12px;
  line-height: 24px;
  letter-spacing: -0.15px;
  text-transform: uppercase;
  color: #FFFFFF; }
  .button button {
    width: 100%;
    height: 100%;
    background-color: transparent;
    border: none; }
    .button button:hover {
      cursor: pointer; }
  .button span {
    position: absolute;
    top: 4px;
    left: 12px; }
  .button-arrow {
    position: absolute;
    top: 11px;
    right: 16px; }
  .button:hover {
    cursor: pointer; }
