.brand .brand__title {
  padding: 16px;
  height: 100%;
  background-color: #FFFFFF; }

.brand {
  padding-bottom: 24px;
  border-bottom: 1px solid #D9FFF5; }

.brand .swiper-slide {
  margin-top: 16px;
  padding-left: 16px; }

.brand__item {
  width: 240px;
  height: 72px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #FFFFFF;
  border-radius: 6px; }
  .brand__item .brand-logo {
    padding-left: 16px; }

.link-btn {
  position: relative;
  width: 40px;
  height: 40px;
  margin-right: 16px; }
  .link-btn .link-btn__circle {
    position: absolute; }
  .link-btn .link-btn__arrow {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: left .3s; }
  .link-btn:hover {
    cursor: pointer; }
    .link-btn:hover .link-btn__arrow {
      left: 60%; }
